button {
  background-color: #01161e;
  border-style: none;
  color: #eff6e0;
  height: 40px;
  width: 92.5%;
  margin-top: 2%;
}

button:active {
  background-color: #010e14;
  color: #eff6e0;
}

input {
  height: 40px;
  width: 90%;
  background-color: #eff6e0;
  margin: 2% 0;
  border-width: 2px;
  border-color: #01161e;
  color: #01161e;
}

input::-webkit-input-placeholder {
  color: #01161e;
  opacity: 75%;
}

input::placeholder {
  color: #01161e;
  opacity: 75%;
}

.main-container {
  background-color: #aec3b0;
  width: 100%;
  height: 100%;
  position: absolute;
}

.container {
  height: 90%;
  width: 80%;
  margin: auto;
}

p {
  margin: 2%;
  color: #01161e;
}

.result {
  font-weight: bold;
}

h4 {
  margin: 2% 0;
}

hr {
  border: 1px solid #01161e;
  margin: 5% 4%;
  opacity: 50%;
}

body {
  background-color: #01161e;
  padding: 0;
  margin: 0 !important;
  text-align: center;
}

.App {
  font-family: sans-serif;
  display: inline-block;
  justify-content: center;
  align-items: center;
  width: 400px;
  height: 500px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: darkcyan;
}

